



























import { Component } from 'vue-property-decorator'
import { VueStrong } from '../typedVue'
import { INotification } from '../interfaces/common.interface'
import { IRootState } from '../interfaces/store/root'
import { mapState } from 'vuex'
import { Watch } from 'vue-property-decorator'

@Component<TheNotification>({
  computed: {
    ...mapState({
      notifications: state => (state as IRootState).notification.notifications
    }),
    width() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return '96%'
        case 'sm':
          return '96%'
        case 'md':
          return '40%'
        case 'lg':
          return '40%'
        case 'xl':
          return '40%'
      }
    }
  }
})
export default class TheNotification extends VueStrong {
  public notifications!: Array<INotification>

  @Watch('notifications.0', { deep: true })
  onNewNotification(val: INotification, oldVal: INotification) {
    this.$notify({ ...val, group: 'notification', duration: 3000 })
    setTimeout(() => {
      this.$store.dispatch('moveToNotificationTray', val.id)
    }, 3000)
  }

  // methods
  moveNotificationToTray(item: Array<any>) {
    console.log(item)
  }
  myFunction(toggle: () => void, notification: INotification) {
    console.log(toggle.toString())
    this.$store.dispatch('moveToNotificationTray', notification.id)
    toggle()
  }
}
