

































































import { Component } from 'vue-property-decorator'
import { mapState } from 'vuex'
import { IRootState } from '../interfaces/store/root'
import { VueStrong } from '../typedVue'

@Component<ResetPassword>({
  created() {
    if (!this.fpEmail) {
      this.$router.push({ name: 'amplifyc-my-account-forgot-password-code-gen' })
    }
  },
  computed: {
    ...mapState({
      fpEmail: state => (state as IRootState).auth.fpEmail,
      fpCode: state => (state as IRootState).auth.fpCode
    })
  }
})
export default class ResetPassword extends VueStrong {
  valid = false
  pwd = ''
  cnfrmpwd = ''
  fpEmail!: string
  fpCode!: string
  showPassword = false
  pwdRules = [
    (value: string) => {
      if (!value) {
        return 'Password is required'
      }
      return true
    }
  ]

  async resetPassword() {
    try {
      await this.$store.dispatch('resetPassword', {
        password: this.pwd,
        confirmPassword: this.cnfrmpwd,
        email: this.fpEmail,
        code: this.fpCode
      })
      this.$store.dispatch('createNotification', {
        group: 'notification',
        title: 'Reset password success. Please login to continue.',
        text: '',
        time: Date.now().toString(),
        data: {
          color: 'success',
          icon: 'mdi-check-decagram'
        }
      })
      this.$router.push({ name: 'amplifyc-my-account-login' })
    } catch (error) {
      console.error(error)
    }
  }
}
